import React from "react";import "./CSS/Volunteer.css";import VolunteerIcon from "../Images/Logos/SubSectionLogo/Volunteer_Logo.jpg";import NSSLogo from "../Images/Logos/OrganizationLogo/NSS_Logo.jpeg";import VolLogo from "../Images/Logos/OrganizationLogo/Vol_logo.png";import SastraLogo from "../Images/Logos/OrganizationLogo/SASTRA_Logo.png";import data from "../DataFiles/data.json";const Volunteer = () => {  const { VolunteerDetails } = data;  const logoMap = {    NSS_Logo: NSSLogo,    Vol_logo: VolLogo,    SASTRALogo: SastraLogo,  };  const isValid = (val) => val !== null && val !== undefined && val !== "";  return (    <div className="volunteer-flex">      {/* Mobile Header */}      <div className="volunteer-mobile-content">        <div className="volunteer-mobile-det">          <img src={VolunteerIcon} alt="Volunteer Icon" className="image-fluid img-icon-sty" />          &nbsp;<span>Volunteer Experience</span>        </div>      </div>      {/* Left Icon Panel */}      <div className="volunteer-left">        <div className="volunteer-logo-sty">          <div className="icon-wrapper">            <img src={VolunteerIcon} alt="Volunteer Icon" className="volunteer-icon" />          </div>          <h4>Volunteer Experience</h4>        </div>      </div>      {/* Right Details Panel */}      <div className="volunteer-right">        <div className="ordered-item-list">          <ul className="ordered-list">            {VolunteerDetails.filter(              (vol) => isValid(vol.Org_Logo) && isValid(vol.Organization_name)            ).map((vol, index) => (              <li className="ordered-item" key={index} style={{ "--i": index }}>                <div className="volunteer-detail-sty">                  <div className="volunteer-logsty">                    <div className="vol-logoicon">                      <img                        src={logoMap[vol.Org_Logo]}                        alt="vol-logo"                        className="vol-logo"                      />                    </div>                    <div className="vol-compsty">                      <div className="text-bold">{vol.Organization_name}</div>                      {(isValid(vol.Role) || isValid(vol.Role_Cause)) && (                        <div className="vol-company-title">                          <p>                            {isValid(vol.Role) ? vol.Role : ""}                            {isValid(vol.Role_Cause) ? ` | ${vol.Role_Cause}` : ""}                          </p>                        </div>                      )}                      <div className="vol-locdursty">                        <div className="vol-assoc-left">                          {vol.Association_with?.is_assoc === "True" &&                          isValid(vol.Association_with.name)                            ? `Associated with: ${vol.Association_with.name}`                            : ""}                        </div>                        <div className="vol-dursty-right">                          {isValid(vol.Duration?.Start_Date?.Month) &&                          isValid(vol.Duration?.Start_Date?.Year)                            ? `${vol.Duration.Start_Date.Month} ${vol.Duration.Start_Date.Year} - ${                                vol.is_present === "True"                                  ? "Present"                                  : isValid(vol.Duration.End_Date?.Month) &&                                    isValid(vol.Duration.End_Date?.Year)                                  ? `${vol.Duration.End_Date.Month} ${vol.Duration.End_Date.Year}`                                  : ""                              }`                            : ""}                        </div>                      </div>                    </div>                  </div>                  {isValid(vol.Description) && (                    <div className="vol-desc">                      <p>{vol.Description}</p>                    </div>                  )}                </div>                <hr className="horizsontalsty" />              </li>            ))}          </ul>        </div>      </div>    </div>  );};export default Volunteer;